<template>
    <div class="img-block flex flex_between">
        <a :href="spaceHref" target="_blank" v-if="spaceHref">
            <img :src="spaceUrl" class="space">
        </a>
        <img-carousel></img-carousel>
    </div>
</template>

<script>
    import imgCarousel from '@/components/index/carousel/imgCarousel'
    import { getFirstImage } from '@/scripts/request'
    export default {
        data() {
            return {
                spaceUrl: '',      // 广告位图片地址
                spaceHref: ''
            }
        },
        created() {
            this.getFirstImg()
        },
        methods: {
            getFirstImg() {
                let _this = this
                let params
                if(window.location.host.includes('cn')) {
                    params = {
                        order: 1
                    }
                } else {
                    params = {
                        order: 2
                    }
                }
                getFirstImage(params).then(res => {
                    if (res.code === 200) {
                        _this.spaceUrl = res.data.image
                        _this.spaceHref = res.data.jump_url
                    }
                })
            }
        },
        components: {
            imgCarousel
        }
    }
</script>

<style lang="less" scoped>
    .img-block {
        margin-bottom: 14px;
        .space {
            width: 205px;
            height: 360px;
        }
    }
</style>



